@import url('https://fonts.googleapis.com/css2?family=Recursive&display=swap');

.main{
    width: 70%;
    border-radius: 20px;
    background-color: #d6dee4;
    align-items: center;
}

.top{
    height: 60px;
    background-color: #424242;
    color: rgb(22, 16, 16);
    padding: 10px;
    border-radius: 20px 20px 0 0;
    font-family: 'Recursive', sans-serif;
    display: flex;
    justify-content: space-between;
}

.header{
    background-color: #424242;
    color: rgb(22, 16, 16);
    margin: 10px 0px 0px 10px;
    font-size: 25px;
    border-radius: 20px 20px 0 0;
    font-family: 'Recursive', sans-serif;
}

.day{
    padding: 15px;
    color: rgb(22, 16, 16);
    font-family: 'Recursive', sans-serif;
    font-size: 24px;
    font-weight: 600;
}

.temp{
    padding: 15px;
    color: rgb(22, 16, 16);
    font-family: 'Recursive', sans-serif;
    font-size: 18px;
}

.flex{
    display: flex;
    justify-content: space-between;
}

.sunrise-sunset{
    padding: 15px;
    color: rgb(22, 16, 16);
    font-family: 'Recursive', sans-serif;
    font-size: 16px;
}

.description{
    padding: 15px;
    color: rgb(22, 16, 16);
    font-family: 'Recursive', sans-serif;
    font-size: 24px;
    font-weight: 600;
}

